import React from 'react';
import { useRouter } from 'shared/hooks';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { Loader } from 'shared/components';
import { CampaignEntity } from 'shared/typings/news.types';
import { CampaigndtHeader } from './components/CampaigndtHeader';
import { CampaigndtClientGroups } from './components/CampaigndtClientGroups';
import { CampaignNewsList } from './components/CampaignNewList';

export function CampaignView() {
	const { params } = useRouter();
	const { companyId } = useAppState();

	const campaignId = parseInt((params as { campaignId: string })?.campaignId);
	if (!companyId) {
		return null;
	}

	return (
		<div className="py-8">
			<h1 className="mb-6 text-2xl font-semibold dark:text-white">Campaign Details</h1>
			<EntityContainer.One
				id={campaignId}
				relations={''}
				entity="campaigns"
				name={`Campaign-${campaignId}`}
				url={`/${companyId}/campaigns/${campaignId}/`}
				params={{}}
			>
				{({ item, isFetched }) => {
					const campaignItem = item as CampaignEntity;
					return (
						<>
							{!isFetched ? (
								<Loader tiled withOverlay />
							) : (
								<>
									<CampaigndtHeader campaignEntity={campaignItem} />
									<CampaigndtClientGroups categories={campaignItem.categories} />
									<CampaignNewsList campaignId={campaignItem.id} />
								</>
							)}
						</>
					);
				}}
			</EntityContainer.One>
		</div>
	);
}
