import React from 'react';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import {
	Loader,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow,
	ActionsButtons,
	ConfirmModal
} from 'shared/components';
import {
	ListHeader,
	Pagination,
	TableBody,
	TableContainer,
	TableHead
} from 'shared/components/Elements';
import { usePagination, useRouter, useSearchQuery } from 'shared/hooks';
import { useCampaignNews } from '../../../hooks';
import { NewsEntity } from 'shared/typings/news.types';
import { Link } from 'react-router-dom';
import dayjs from 'dayjs';
import { useAuth } from '../../../../../modules/auth/hooks/useAuth';

type Props = {
	campaignId: number;
};

export function CampaignNewsList({ campaignId }: Props) {
	const { companyId, isPermitted } = useAppState();
	const { page, handlePage } = usePagination();
	const { query, setQuery, searchQuery } = useSearchQuery();
	const { match, history } = useRouter();

	const { userData } = useAuth();
	const canSendNews = userData?.can_send_news;

	const {
		handleCampaignNewsDelete,
		deleteVisible,
		showDeleteVisible,
		deleteLoading,
		closeDeletePopup
	} = useCampaignNews();

	const setQueryHandler = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) =>
		setQuery(event.target.value);

	const deleteCampaignNewsHandler = () => () => handleCampaignNewsDelete(deleteVisible!);

	return (
		<div className="relative mb-8">
			<ListHeader
				title="Campaign News"
				query={query}
				placeholder="Search For News"
				handleSearch={setQueryHandler}
				btnHandler={() => history.push(`/news/campaigns/news/create/${campaignId}`)}
				btnText="Add News"
				isPermitted={isPermitted || !canSendNews}
			/>
			{companyId ? (
				<EntityContainer.All
					entity="news"
					name="AllCampaignNews"
					url={`/${companyId}/notifications/`}
					params={{
						page,
						limit: 5,
						extra: {
							search: searchQuery,
							campaign: campaignId
						}
					}}
				>
					{({ items, isFetched, meta }) => {
						const newsItems = items as NewsEntity[];
						return (
							<>
								<div>
									<TableContainer classes={{ root: 'relative ' }}>
										<StyledTable
											aria-label="simple table"
											classes={{ root: 'table-fixed dark-mode-styled-table' }}
										>
											<TableHead>
												<StyledTableRow>
													<StyledTableHeadCell style={{ width: '20%' }}>Title</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '10%' }}>Status</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '10%' }}>
														Frequency
													</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '20%' }}>Author</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '20%' }}>Date</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '20%' }}>
														<div className="ml-2 mb-2">Actions</div>
													</StyledTableHeadCell>
												</StyledTableRow>
											</TableHead>
											<TableBody>
												{newsItems.map((item) => {
													const { frequency, start_time, status, author, subject, id } = item ?? {};
													return (
														<StyledTableRow key={id}>
															<StyledTableCell>
																<Link to={`${match.url}/${item.id}`}>
																	<span className="text-primary transition hover:text-primary-dark">
																		{subject}
																	</span>
																</Link>
															</StyledTableCell>
															<StyledTableCell>{status}</StyledTableCell>
															<StyledTableCell>{frequency}</StyledTableCell>
															<StyledTableCell>{author.name}</StyledTableCell>
															<StyledTableCell>
																{dayjs(start_time).format('MMMM D YYYY, h:mm a')}
															</StyledTableCell>
															<StyledTableCell>
																<div className="flex items-center justify-end ">
																	<ActionsButtons.DeleteButton
																		clickHandler={() => showDeleteVisible(item)}
																		isDisabled={!isPermitted || !canSendNews}
																	/>
																	<ActionsButtons.ViewButton
																		clickHandler={() =>
																			history.push(`/news/campaigns/view/${campaignId}/${item.id}`)
																		}
																		isDisabled={!isPermitted || !canSendNews}
																	/>
																	<ActionsButtons.EditButton
																		clickHandler={() =>
																			history.push(`/news/campaigns/edit/${campaignId}/${item.id}`)
																		}
																		isDisabled={!isPermitted || !canSendNews}
																	/>
																</div>
															</StyledTableCell>
														</StyledTableRow>
													);
												})}
											</TableBody>
										</StyledTable>
										{!isFetched && <Loader tiled withOverlay />}
									</TableContainer>
									<ConfirmModal
										featureHandler={deleteCampaignNewsHandler()}
										isOpen={Boolean(deleteVisible)}
										toggleHandler={closeDeletePopup}
										title={`Are you sure to delete  ${deleteVisible?.subject}?`}
										hasLoader
										isLoading={deleteLoading}
									/>
									<div className="news__pagination my-4">
										{items.length ? (
											<Pagination
												count={meta?.last_page}
												page={page ? +page : 1}
												defaultPage={6}
												siblingCount={1}
												color={'primary'}
												onChange={handlePage}
											/>
										) : null}
									</div>
								</div>
							</>
						);
					}}
				</EntityContainer.All>
			) : null}
		</div>
	);
}
