import React from 'react';
import { CampaignEntity, CampaignEntityClientGroup } from 'shared/typings/news.types';
import { TableBody, TableContainer, TableHead } from 'shared/components/Elements';
import {
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
type Props = {
	categories: CampaignEntityClientGroup[];
};
export function CampaigndtClientGroups({ categories }: Props) {
	return (
		<div className="mb-8">
			<h2 className="mb-6 text-xl font-bold dark:text-white">Client Groups</h2>
			<TableContainer classes={{ root: 'relative ' }}>
				<StyledTable
					aria-label="simple table"
					classes={{ root: 'table-fixed dark-mode-styled-table' }}
				>
					<TableHead>
						<StyledTableRow>
							<StyledTableHeadCell style={{ width: '5%' }}>#</StyledTableHeadCell>
							<StyledTableHeadCell style={{ width: '95%' }}>Client Group</StyledTableHeadCell>
						</StyledTableRow>
					</TableHead>
					<TableBody>
						{((categories as CampaignEntity[]) ?? []).map((item, idx) => {
							const { name, id } = item;
							return (
								<StyledTableRow key={id}>
									<StyledTableCell>{idx + 1}</StyledTableCell>
									<StyledTableCell>{name}</StyledTableCell>
								</StyledTableRow>
							);
						})}
					</TableBody>
				</StyledTable>
			</TableContainer>
		</div>
	);
}
